.page {
  font-family: Roboto, Helvetica, sans-serif;
}

.page__body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  display: block;
}

.nav {
  font-size: 12px;
  font-weight: 500;
}

.nav__list {
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link--active {
  color: #00acdc;
}

.nav__link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  margin: 0 0 24px;
}

.product-card__price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.product-card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy-btn {
  color: #fff;
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.product-card__buy-btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.reviews__stars {
  gap: 4px;
  display: flex;
}

.reviews__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.reviews__star--active {
  background-image: url("star-active.edda4c43.svg");
}

.reviews__amount {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}
